<template>
<v-container grid-list-xl class="designs-component">
  <v-layout column>
    <h3 class="text-uppercase headline">Designs</h3>
    <v-layout mt-4 row fill-height wrap >
      <v-layout v-if="isLoading" justify-center row class="mt-4">
        <Preloader size="80"/>
      </v-layout>
      <v-flex v-else xs12 sm6 md4 lg3 v-for="item in designs" class="card">
        <v-card class="design-card" tile flat @click="selectDesign(item)">
          <v-layout justify-center>
            <v-flex xs10 class="design-wrapper" :style="{'background': item.background_color}">
              <v-img
                class="design-image"
                :src="item.activeImage"
                @error="onImageLoadingError(item)"
              ></v-img>
            </v-flex>
          </v-layout>
          <span class="headline">{{ item.name }}</span>
        </v-card>
      </v-flex>
    </v-layout>
  </v-layout>
</v-container>
</template>

<script>
  import Preloader from "../components/Preloader";
  export default {
    name: 'DesignsPage',
    components: {Preloader},
    created() {
      this.fetchDesigns();
      this.fetchNamesList();
    },
    computed: {
      isLoading() {
        return this.$store.state.designs.isLoading;
      },
      designs() {
        if (this.$store.state.namesList.isLoading) return [];
        let name = this.$route.params.name;
        let noImages = this.$store.state.designs.noImages[name] || [];
        let namesList = this.$store.state.namesList.list;
        let designsList = this.$store.state.designs.list;
        if (!namesList.find(item => item.slug === name)) {
          return designsList.map(item => Object.assign(item, { activeImage: item.image }));
        }
        // replace logo default variant with demo, if it wasn't loaded
        return designsList.map((item) => {
          if (noImages.indexOf(item.slug) !== -1) {
            item.activeImage = item.image;
          } else {
            item.activeImage = item.image.replace(/name(.[\w]+$)/, name + '$1');
          }
          return item;
        });
      }
    },
    methods: {
      fetchDesigns() {
        this.$store.dispatch('fetch', { key: 'designs', slug: this.$route.params.subcategory });
      },
      fetchNamesList() {
        this.$store.dispatch('fetchIfEmpty', { key: 'namesList' });
      },
      selectDesign(item) {
        this.$router.push({ name: 'products', params: {
          ...this.$route.params,
          design: item.slug
        }});
      },
      onImageLoadingError(item) {
        this.$store.commit('setNoDesignImage', { nameSlug: this.$route.params.name, designSlug: item.slug });
      }
    }
  } ;
</script>
<style lang="scss">
  .designs-component {
    .design-card {
      cursor: pointer;
    }
    .design-image {
      max-width: 300px;
      margin-left: auto;
      margin-right: auto;
      min-height: 100px;
    }
    .design-wrapper {
      border-radius: 5px;
    }
  }
</style>
